
import { Container, Grid } from "@mui/material";
import ResultTitle from "../components/Result/ResultTitle";
import Graph from "../components/Result/Graph";
import { useLocation } from "react-router-dom";
import React from 'react';
import myImage from '../images/myImage.jpg';
import { Typography } from "@mui/material";

// 시간과 계절에 따른 전기요금을 계산하는 함수
const getElectricityRate = (hour, season) => {
  let rate;

  switch(season){
    case "1": //여름
      if ((hour >= 0 && hour < 8) || (hour >= 22 && hour <= 24)) rate = 70.0;
      else if ((hour >= 8 && hour < 9) || (hour >= 12 && hour < 16) || (hour >= 19 && hour < 22)) rate = 121.2;
      else rate = 143.7;
      break;

    case "0": // 봄
    case "2": // 가을
      if ((hour >= 0 && hour < 8) || (hour >= 22 && hour <= 24)) rate = 70.0;
      else if ((hour >= 8 && hour < 9) || (hour >= 12 && hour < 16) || (hour >= 19 && hour < 22)) rate = 77.4;
      else rate = 88.7;
      break;
  
    case "3": // 겨울
      if ((hour >= 0 && hour < 8) || (hour >= 22 && hour <= 24)) rate = 78.7;
      else if ((hour >= 8 && hour < 9) || (hour >= 12 && hour < 16) || (hour >= 19 && hour < 22)) rate = 109.1;
      else rate = 123.9;
      break;

    default:
      rate = 100; // 기본 요금   
  }
  
  return rate/60; // 기본 요금
};

// 지정된 시간에 따른 설정 온도를 반환하는 함수
const getScheduleTemperature = (schedules, hour, minute) => {
  console.log("Schedules:", schedules); // 로그 추가
  if (!Array.isArray(schedules)) {
    return 5; // 잘못된 형식의 경우 기본 설정 온도 반환
  }
  for (const schedule of schedules) {
    if (!schedule || !schedule.startTime || !schedule.endTime) {
      continue; // 잘못된 스케줄은 무시
    }
    const startHour = parseInt(schedule.startTime.split(":")[0], 10);
    const startMin = parseInt(schedule.startTime.split(":")[1], 10);
    const endHour = parseInt(schedule.endTime.split(":")[0], 10);
    const endMin = parseInt(schedule.endTime.split(":")[1], 10);


    if (
      (hour > startHour || (hour === startHour && minute >= startMin)) &&
      (hour < endHour || (hour === endHour && minute < endMin))
    ) {
      return parseFloat(schedule.temperature);
    }
  }
  return 5; // 기본 설정 온도
};

// 지정된 시간에 해당하는 외부온도를 반환하는 함수
const getOutdoorTemperature = (outdoorTemps, hour, minute) => {
  if (!Array.isArray(outdoorTemps) || outdoorTemps.length === 0) {
    return 25; // 배열이 아니거나 비어있으면 기본 온도 반환
  }

  const currentTime = hour * 60 + minute; // 현재 시간을 분으로 변환

  for (const temp of outdoorTemps) {
   
    const startHour = parseInt(temp.start.split(":")[0]);
    const startMinute = parseInt(temp.start.split(":")[1]);
    const endHour = parseInt(temp.end.split(":")[0]);
    const endMinute = parseInt(temp.end.split(":")[1]);
    const startTime = startHour * 60 + startMinute;
    const endTime = endHour * 60 + endMinute;

    if (currentTime >= startTime && currentTime < endTime) {
      console.log("Selected Temp:", temp);  // 반환되는 온도 로깅
      return parseFloat(temp.temp); // 해당 시간대의 외부온도를 반환
    }
  }

   // 시간대를 찾지 못했을 경우 기본값으로 25도를 반환
   return 25; // 설정되지 않은 시간대에 대한 기본 온도
};



// 입력된 데이터를 기반으로 다양한 그래프 데이터를 계산하는 함수
const calculateData = (input, doorOpenTimes) => {
  if (!input || !input.schedules) return {};
  //외부온도 배열 검사
  const outdoorTemps = Array.isArray(input.outdoorTemps) ? input.outdoorTemps : [];

  const startHourTime = parseInt(input.startTime.split(":")[0], 10);
  const startMinTime = parseInt(input.startTime.split(":")[1], 10);
  const endHourTime = parseInt(input.endTime.split(":")[0], 10);
  const endMinTime = parseInt(input.endTime.split(":")[1], 10);
  // const defHourTime= parseInt(input.defrostabsTimes[].split(":")[0], 10);
  // const defMinTime= parseInt(input.defrostabsTime.split(":")[1], 10);
  const mins = (endHourTime - startHourTime) * 60 + (endMinTime - startMinTime);
  const massEffect = Math.log(parseFloat(input.mass));

  let temp = parseFloat(input.currentIn);
  const refri_out = parseFloat(input.out);
  const refri_size= parseFloat(input.size)/5.93;
  
  //const ext_temp=parseFloat(input.currentOut);
  const temperatureData = [];
  const powerUsageData = [];
  const electricityCostData = [];
  const yDataSetTemperature = [];
  let cooling = false;
  let lesscooling=false;
  let slowcooling = false;
  let slowcoolingCounter = 0;
  let defrostTimer = 0; // 제상 타이머
  let defrosting = false; // 제상 중인지 여부
  let coolingDuringDefrost = false; // 제상 중 냉각 여부
  let defrostCoolingCounter = 0; // 제상 중 냉각 카운터
  let coolingDownAfterDefrost = false;
  let coolingPauseCounter = 0;
  let shortCoolingCounter = 0;
  let CoolingCounter=0;
  let coolingPauseDuration = 0;
  let defr_cycle=parseInt(input.defrostCycle); 
  let defr_du=parseInt(input.defrostDuration); 


  for (let i = 0; i <= mins; i++) {
    let O = 0;

    const currentHour = startHourTime + Math.floor((startMinTime + i) / 60);
    const currentMinute = (startMinTime + i) % 60;
    const currentTime = `${currentHour.toString().padStart(2, "0")}:${currentMinute.toString().padStart(2, "0")}`;
    
   

    if (doorOpenTimes.includes(currentTime)) {
      O = 1;
    } else {
      O = 0;
    }

    let timestamp = startHourTime * 60 + startMinTime + i;
    let hour = parseInt(timestamp / 60);
    let minute = timestamp % 60;

    // 현재 시간에 해당하는 외부온도를 가져오는 코드
    const ext_temp = getOutdoorTemperature(input.outdoorTemps, hour, minute);
    

    const scheduleTemperature = getScheduleTemperature(input.schedules, hour, minute);
    yDataSetTemperature.push(scheduleTemperature);

    let startCoolingTemp, stopCoolingTemp;
    if (hour >= 7 && hour < 23) {
      startCoolingTemp = scheduleTemperature + 1.5;
      stopCoolingTemp = scheduleTemperature - 1.5;
    } else {
      startCoolingTemp = scheduleTemperature + 2.7;
      stopCoolingTemp = scheduleTemperature - 0.8;
    }
    // if (temp >= startCoolingTemp) {
    //   cooling = true;
    // } else if (temp <= stopCoolingTemp) {
    //   cooling = false;
    // }
    if (temp <= stopCoolingTemp && cooling&&!slowcooling) {
      cooling = false;
      coolingPauseDuration = 2; // 2분 동안 냉각을 멈춤
      // document.writeln("stop1");
    }else if ((coolingPauseDuration === 1) && temp < startCoolingTemp && !shortCoolingCounter&&!slowcooling) {
      lesscooling = true;
      shortCoolingCounter = 1; // 1분 동안 냉각
      // document.writeln("cool");
    }
    else if (shortCoolingCounter > 0 && !cooling&&!slowcooling) {
      shortCoolingCounter--;
      lesscooling = false;
      coolingPauseDuration--;
    }
    
    else if ((coolingPauseDuration === 0 ||1) && temp >= startCoolingTemp && !cooling&&!slowcooling) {
      cooling = true;
      slowcooling = true;
      slowcoolingCounter = 3; // 설정온도 + margin에 도달하면 냉각 시작
      if(coolingPauseDuration===1){
        coolingPauseDuration--;
      }
      // document.writeln("coolstart");
    }
    else if (coolingPauseDuration > 0&&!slowcooling) {
      coolingPauseDuration--;
      // document.writeln("stopnum");
    }
    else if(shortCoolingCounter==0 && coolingPauseDuration==0&&!lesscooling&&!cooling&&!slowcooling){
      coolingPauseDuration=1;
    }
  
    


    
    // 제상 로직
    if (input.defrost && defrostTimer >= defr_cycle) {
      defrosting = true;
      defrostTimer = 0;
    }
    
    // if (input.defrostabs && currentHour==defHourTime&&currentMinute==defMinTime) {
    //   defrosting = true;
    //   defrostTimer = 0;
    // }

    let abslength=input.defrostabsTimes.length;
    
    // defrostabsTimes 배열의 길이를 확인하고 요소에 접근
    if (abslength > 0) {
      for(let i=0;i<abslength;i++){
        if (input.defrostabs && currentHour==parseInt(input.defrostabsTimes[i].absTime.split(":")[0], 10)&&currentMinute==parseInt(input.defrostabsTimes[i].absTime.split(":")[1], 10)) {
          defrosting = true;
          defrostTimer = 0;
        }
      }
      
    }
    

    
   

    if ((input.defrostabs && defrosting)||(input.defrost && defrosting)) {
      if (temp >= 8) {
        coolingDuringDefrost = true;
      } else if (temp <= 7) {
        coolingDuringDefrost = false;
      }

      if (coolingDuringDefrost) {
        if (defrostCoolingCounter < 1) {
          temp =
            temp +
            ((-38000*0.6 +21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
          
          if (currentHour >= 9 && currentHour < 18) {
            powerUsageData.push(895);
          }else{
            powerUsageData.push(747);
          }
          defrostCoolingCounter += 1;
          
        } else {
          temp =
            temp +
            (21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp)) /
              (massEffect  * 3900));
          powerUsageData.push(4);
          defrostCoolingCounter = 0;
        }
      } else {
        temp =
          temp +
          (21 *(12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp)) /
            (massEffect  * 3900));
        powerUsageData.push(4);
      }
      let duration;
      if(input.defrost&&!input.defrostabs){
        duration=defr_du;
      }
      else if(!input.defrost&&input.defrostabs){
        duration=parseInt(input.defrostabsDuration);
      }
      else{
        duration=Math.max(parseInt(input.defrostabsDuration),defr_du);
      }

      if (defrostTimer >= duration) {
        defrosting = false;
        coolingDuringDefrost = false;
        coolingDownAfterDefrost = true;
      }
    } else {
      if (slowcooling) {
        if (coolingDownAfterDefrost && temp <= 7.5) {
          temp =
            temp +
            ((-24000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
          powerUsageData.push(413);
          slowcoolingCounter--; // 제상 후 냉각 중이고 온도가 5.5도 이하일 때
        } else {
          temp =
            temp +
            ((-32000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
              // 00:00에서 9:00 사이에 powerUsageData를 랜덤하게 설정
          
          if (currentHour >= 9 && currentHour < 18) {
            powerUsageData.push(747);
            slowcoolingCounter--;
          }else{
            powerUsageData.push(598);
            slowcoolingCounter--;
          }
        }
        if (slowcoolingCounter === 0) {
          slowcooling = false;
        }
        if (coolingDownAfterDefrost && temp <= 6.9) {
          coolingDownAfterDefrost = false; // 온도가 5.5도 이하로 내려가면 냉각 중 상태 해제
        }
      } 
      else if (cooling) {
        if (coolingDownAfterDefrost && temp <= 7.5) {
          temp =
            temp +
            ((-30000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
          powerUsageData.push(362); // 제상 후 냉각 중이고 온도가 5.5도 이하일 때
        } else {
          temp =
            temp +
            ((-50000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
              // 00:00에서 9:00 사이에 powerUsageData를 랜덤하게 설정
          
          if (currentHour >= 9 && currentHour < 18) {
            powerUsageData.push(895);
          }else{
            powerUsageData.push(747);
          }
        }
  
        if (coolingDownAfterDefrost && temp <= 6.9) {
          coolingDownAfterDefrost = false; // 온도가 5.5도 이하로 내려가면 냉각 중 상태 해제
        }
      } 
      else if(lesscooling){
        if (coolingDownAfterDefrost && temp <= 7.5) {
          temp =
            temp +
            ((-25000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
          powerUsageData.push(362); // 제상 후 냉각 중이고 온도가 5.5도 이하일 때
        } else {
          temp =
            temp +
            ((-29000*0.6 + 21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp))) /
              (massEffect * 3900));
              // 00:00에서 9:00 사이에 powerUsageData를 랜덤하게 설정
          
          if (currentHour >= 9 && currentHour < 18) {
            powerUsageData.push(747);
          }else{
            powerUsageData.push(598);
          }
        }
      } 
      
      else {
        temp =
          temp +
          (21 * (12 * refri_size * (ext_temp - temp) + O * 1000 * (ext_temp - temp)) /
            (massEffect * 3900));
        powerUsageData.push(5.5);
      }
      if (coolingDownAfterDefrost && temp <= 6.9) {
        coolingDownAfterDefrost = false; // 온도가 5.5도 이하로 내려가면 냉각 중 상태 해제
      }
    }

    //실외기실 외부온도 35도 이상시 비정상적인 전력량
    if(refri_out>= 35){
      // 9:00에서 18:00 사이에 15분 간격으로 전력 사용량을 랜덤하게 설정
      if (currentHour >= 9 && currentHour < 18 && currentMinute % 15 === 0 && !coolingDownAfterDefrost) {
        powerUsageData[i] = Math.floor(Math.random() * (120000/36 - 90000/36 + 1)) + 90000/36; // 90000에서 120000 사이의 랜덤한 값
      } else if (currentHour >= 9 && currentHour < 18 && (currentMinute - 1) % 15 === 0 && !coolingDownAfterDefrost) {
        powerUsageData[i] = 2000; // 1분 후에 전력 사용량을 90000으로 설정
      } 

    }

    

    defrostTimer += 1;

    // 전력 사용량(kW) 계산
    const powerUsage = powerUsageData[i] / 1000; // W를 kW로 변환
    // 전기요금 계산
    electricityCostData.push(powerUsage * getElectricityRate(hour, input.season));

    temperatureData.push({
      hour: parseInt(timestamp / 60).toString().padStart(2, "0"),
      minute: (timestamp % 60).toString().padStart(2, "0"),
      temperature: temp,
    });
  }

  const xData = temperatureData.map((data) => data.hour + ":" + data.minute);
  const yDataTemperature = temperatureData.map((data) => parseFloat(data.temperature));

  return {
    xData: xData,
    yDataTemperature: yDataTemperature,
    yDataSetTemperature: yDataSetTemperature,
    powerUsageData: powerUsageData,
    electricityCostData: electricityCostData,
    showAnnotations: input.showAnnotations,
  };
};

// 결과 페이지를 구성하는 메인 컴포넌트
export default function Result() {
  const location = useLocation();
  const input = location.state || {};
  const doorOpenTimes = input.doorOpenTimes || [];

  // calculateData 함수를 사용하여 그래프 데이터를 계산
  const data = calculateData(input, doorOpenTimes);

   // 계산된 데이터를 구조 분해 할당을 통해 변수에 저장
  const { xData = [], yDataTemperature = [], yDataSetTemperature = [], powerUsageData = [], electricityCostData = [] } = calculateData(input, doorOpenTimes);

  // 전력 사용량과 전기요금의 총합을 계산
  const totalPowerUsage = powerUsageData.reduce((acc, curr) => acc + curr, 0);
  const totalPowerCost = electricityCostData.reduce((acc, curr) => acc + curr, 0);

  // 수정된 코드:
  const { defrostCycle, defrostDuration } = input;
  let defrostabsTimes = input.defrostabsTimes || []; // defrostabsTimes 배열이 없으면 빈 배열을 할당

  return (
    <>
      <Container
        maxWidth="lg"
        sx={{
          display: "flex",
          flexDirection: "column",
          mt: "calc(3vh)",
        }}
      >
        <ResultTitle />
      </Container>
      <Container>
        <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 1, md: 10 }}>
          <Grid item xs={12} sm={12}>
            <Graph title={"냉장고 내부 온도 & 설정 온도"} 
            xData={xData} 
            yData={yDataTemperature} 
            yData2={yDataSetTemperature}
            showAnnotations={data.showAnnotations} 
            yAxisTitle="온도 (°C)"/>
          </Grid>
          
          <Grid item xs={12} sm={12}>
            <Graph title={"전력사용량"} 
            xData={xData}
            yData={powerUsageData}
            showAnnotations={data.showAnnotations}   
            yAxisTitle="전력 (W)" 
            />
          </Grid>
          <div>
            <p>Total Power Usage(W): {totalPowerUsage}</p>
          </div>
          <div><p>  // </p></div>
          <div>
            <p>Total Power Usage(kwh): {totalPowerUsage/60000}</p>
          </div>
          <Grid item xs={12} sm={12}>
            <Graph title={"전기요금"} 
            xData={xData} 
            yData={electricityCostData}
            showAnnotations={data.showAnnotations}  
            yAxisTitle="전기요금 (원)" 
            />
          </Grid>
        </Grid>
        <div>
          <p>Total Power Cost: {totalPowerCost}</p>
          {/* <p>제상 주기 {parseInt(defrostCycle)}</p>
          <p>제상 유지 시간 {parseInt(defrostDuration)}</p>
          <p>{parseInt(defrostabsTimes[0].absTime.split(":")[0], 10)}</p>
          <p>{parseInt(defrostabsTimes[0].absTime.split(":")[1], 10)}</p>
          <p>{parseInt(defrostabsTimes[1].absTime.split(":")[0], 10)}</p>
          <p>{parseInt(defrostabsTimes[1].absTime.split(":")[1], 10)}</p> */}
        </div>
        <div>
            <Typography variant="h6" align="left">부하시간대에 대한 가격표</Typography> <br></br>
            <img src={myImage} alt="Description" width='400' style={{ display: 'block' }} />
          </div>
      </Container>
    </>
  );
}