
import {
  Checkbox,
  FormControlLabel,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  MenuItem,
  TextField,
} from "@mui/material";
import { useState } from "react";
import React from "react";
import { Controller, useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import Papa from 'papaparse';


export default function InputDialog({ open, handleClose }) {
  // 페이지 이동을 위한 useNavigate 훅을 사용
  const navigate = useNavigate();
  // useForm 훅을 사용하여 폼 관련 기능들을 구현. 폼의 기본값들을 설정
  const { register, handleSubmit, watch, control, setValue, getValues } = useForm({
    defaultValues: {
      defrost: false,
      defrostabs: false,
      defrostabsTimes:[],
      defrostabsDuration: "0",
      defrostCycle: "0",
      defrostDuration: "0",
      out: "25",
      size: "19.56",
      mass: "297",
      season: "1",
      startTime: "00:01",
      endTime: "23:59",
      currentIn: "6",
      currentOut: "25",
      schedules: [
      ]
    }

  });

  //'defrost' 체크박스의 상태를 변경하는 함수
  const handleDefrostChange = (event) => {
    setValue('defrost', event.target.checked);
    // 'defrostabs' 체크박스 상태를 false로 설정
    setValue('defrostabs', false);
  };

  // 'defrostabs' 체크박스의 상태를 변경하는 함수
  const handleDefrostAbsChange = (event) => {
    setValue('defrostabs', event.target.checked);
    // 'defrost' 체크박스 상태를 false로 설정
    setValue('defrost', false);
  };

  


  const defrost = watch("defrost"); // 이렇게 하면 "defrost" 필드의 값을 실시간으로 관찰할 수 있음 .

   // 시간 값을 포맷하는 함수. 예: "9:00"을 "09:00"으로 변경
  const formatTime = (time) => {
    if (!time) { // 시간 값이 유효하지 않으면, 기본 값 또는 오류 처리를 수행.
      return "00:00"; // 예시로 기본 값을 "00:00"으로 설정
    }
    const [hours, minutes] = time.split(":");
    const formattedHours = hours.length === 1 ? `0${hours}` : hours;
    return `${formattedHours}:${minutes}`;
  };

  // CSV 파일의 데이터를 formData 형식으로 변환하는 함수
  const transformCsvDataToFormData = (csvData) => {
    // csvData 배열을 순회하면서 필요한 형태로 데이터를 변환
    return csvData.map((row, index) => ({
      id: index,
      startTime: formatTime(row[0]),
      endTime: formatTime(row[1]),
      temperature: row[2],

    }));
  };


  // 파일 업로드 이벤트를 처리하는 함수
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      // 파일의 유형에 따라 처리 방식을 구분 (JSON 또는 CSV).
      if (file.type === "application/json") {
        const reader = new FileReader();
        reader.onload = (e) => {
          const jsonContent = JSON.parse(e.target.result);

          // defrost 체크박스 설정
          if (typeof jsonContent.defrost === 'boolean') {
            setValue('defrost', jsonContent.defrost);
          }

          // season 선택 설정
          if (jsonContent.season) {
            setValue('season', jsonContent.season.toString()); // "0", "1", "2", "3" 중 하나여야 합니다.
          }

          // JSON 파일에서 기본 입력 값 설정
          Object.keys(jsonContent).forEach(key => {
            if (key !== 'schedules') {
              setValue(key, jsonContent[key]);
            }
          });

          // 스케줄 데이터 처리
          if (jsonContent.schedules && Array.isArray(jsonContent.schedules)) {
            const transformedSchedules = jsonContent.schedules.map((item, index) => ({
              ...item,
              id: index,
              startTime: formatTime(item.startTime),
              endTime: formatTime(item.endTime),
            }));
            setFormData(transformedSchedules);
            transformedSchedules.forEach((data, index) => {
              setValue(`schedules[${index}].startTime`, data.startTime);
              setValue(`schedules[${index}].endTime`, data.endTime);
              setValue(`schedules[${index}].temperature`, data.temperature);
            });
          }

          // 시간대별 외부온도 데이터 처리
          if (jsonContent.outdoorTemps && Array.isArray(jsonContent.outdoorTemps)) {
            setOutdoorTemps(jsonContent.outdoorTemps);
          }
        };
        reader.readAsText(file);
      } else if (file.type === "text/csv") {
        // CSV 파일 처리 (기존 로직)
        Papa.parse(file, {
          complete: function (results) {
            // CSV 데이터를 formData 형식으로 변환
            const transformedData = transformCsvDataToFormData(results.data);
            // formData 상태 업데이트
            setFormData(transformedData);

            // 외부온도 데이터 추출 및 상태 업데이트
            const outdoorTempsData = results.data.map(row => ({
              id: row[0], // 시작 시간을 ID로 사용
              start: formatTime(row[0]),
              end: formatTime(row[1]),
              temp: row[3]
            }));
            setOutdoorTemps(outdoorTempsData);
            // react-hook-form의 setValue를 사용하여 필드 값 설정
            transformedData.forEach((data, index) => {
              setValue(`schedules[${index}].startTime`, data.startTime);
              setValue(`schedules[${index}].endTime`, data.endTime);
              setValue(`schedules[${index}].temperature`, data.temperature);
              setValue(`schedules[${index}].outdoorTemps`, data.outdoorTemps);
            });
          },
          header: false
        });
      }
    }
  };


  // 폼 데이터에 대한 초기 상태를 설정
  const initialSchedules = [
    { startTime: "08:00", endTime: "09:00", temperature: "6" },
    { startTime: "09:00", endTime: "12:00", temperature: "7" }
  ];

   // useState를 사용하여 동적 폼 필드(스케줄)를 관리하는 상태를 설정
  const [formData, setFormData] = useState(initialSchedules.map((schedule, index) => ({
    id: index,
    value: {
      start: schedule.startTime,
      end: schedule.endTime,
      tem: schedule.temperature,
    }
  })));

  // 추가적인 스케줄 입력란을 관리하기 위한 상태
  const [nextId, setNextId] = useState(2);

  // 새로운 스케줄 입력란을 추가하는 함수
  const handleAddInput = () => {
    setFormData([...formData, {
      id: nextId,
      startTime: "",
      endTime: "",
      temperature: ""
    }]);
    setNextId(nextId + 1);
  };


  // 스케줄 입력란의 값을 변경하는 함수
  const handleChange = (id, field, value) => {
    const updatedFormData = formData.map((item) => {
      if (item.id === id) {
        return { ...item, [field]: value }; // 수정된 부분
      }
      return item;
    });
    setFormData(updatedFormData);
  };



  // 스케줄 입력란을 제거하는 함수
  const handleRemoveInput = (id) => {
    console.log("Removing ID:", id);
    setFormData(formData.filter((data) => data.id !== id));
  };

  // 계절 선택 옵션 배열
  const seasonOption = [
    {
      value: "0",
      label: "봄",
    },
    {
      value: "1",
      label: "여름",
    },
    {
      value: "2",
      label: "가을",
    },
    {
      value: "3",
      label: "겨울",
    },
  ];
  // 시간대별 외부온도 상태를 관리하는 부분
  const [outdoorTemps, setOutdoorTemps] = useState([
    { id: 0, start: '08:00', end: '09:00', temp: '25' },
    { id: 1, start: '09:00', end: '12:00', temp: '25' },
  ]);

  // 다음 시간대별 외부온도 ID를 추적하는 상태
  const [nextOutdoorTempId, setNextOutdoorTempId] = useState(2);

  // 시간대별 외부온도 입력란을 추가하는 함수
  const handleAddOutdoorTemp = () => {
    const newOutdoorTemp = { id: nextOutdoorTempId, start: '', end: '', temp: '' };
    setOutdoorTemps([...outdoorTemps, newOutdoorTemp]);
    setNextOutdoorTempId(nextOutdoorTempId + 1);
  };

  // 시간대별 외부온도 입력란을 변경하는 함수
  const handleChangeOutdoorTemp = (id, field, value) => {
    const updatedOutdoorTemps = outdoorTemps.map((temp) =>
      temp.id === id ? { ...temp, [field]: value } : temp
    );
    setOutdoorTemps(updatedOutdoorTemps);
  };

  // 시간대별 외부온도 입력란을 제거하는 함수
  const handleRemoveOutdoorTemp = (id) => {
    setOutdoorTemps(outdoorTemps.filter((temp) => temp.id !== id));
  };

  //  doorTime 상태 관리 추가
  const [doorTime, setDoorTime] = useState("");

  //문열림 이벤트 관리하기 위해 useState 추가
  const [doorOpenTimes, setDoorOpenTimes] = useState([]);

  //문열림 이벤트 추가 및 삭제 함수
  const handleAddDoorOpenEvent = () => {
    console.log("Added Door Open Time:", doorTime);
    setDoorOpenTimes(prevTimes => [...prevTimes, doorTime]);
  }

  const handleRemoveDoorOpenEvent = (index) => {
    const newDoorOpenTimes = [...doorOpenTimes]
    newDoorOpenTimes.splice(index, 1);
    setDoorOpenTimes(newDoorOpenTimes);
  }




  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{ sx: { borderRadius: "15px", width: "calc(70vw)" } }}
      >
        <form
          onSubmit={handleSubmit((data) => {
            // 제출 데이터 처리
            const submissionData = {
              ...data,
              doorOpenTimes,
              schedules: formData, // 직접 수정된 부분 없이 formData 배열을 그대로 사용
              outdoorTemps, // 추가된 부분

            };
            console.log("Submitting Data:", submissionData); // 로그로 제출 데이터 확인
            navigate("/result", {
              state: submissionData, // 수정된 부분
            });
          }
          )}
        >
          <DialogTitle>User Input Info</DialogTitle>
          <DialogContent>
            <FormControlLabel
              control={
                <Checkbox
                  {...register("defrost")}
                  checked={!!watch("defrost")} // "!!"를 사용하여 얻은 값이 boolean 타입이 되도록 강제합니다.
                  onChange={handleDefrostChange}
                />
              }
              label="제상 기능 사용"
            />
            <FormControlLabel
              control={
                <Checkbox
                  {...register("defrostabs")}
                  checked={!!watch("defrostabs")} // "!!"를 사용하여 얻은 값이 boolean 타입이 되도록 강제합니다.
                  onChange={handleDefrostAbsChange}
                />
              }
              label="제상 절대 시간 기능 사용"
            />

            <br></br>
            {watch("defrost")&&(
              <>
              <TextField
                margin="dense"
                id="defrostCycle"
                label="제상주기"
                fullWidth
                variant="standard"
                {...register("defrostCycle")}
              />
              <TextField
                margin="dense"
                id="defrostDuration"
                label="제상유지시간"
                fullWidth
                variant="standard"
                {...register("defrostDuration")}
              />
              </>
            )}
            
            {/* <TextField
              type="time"
              margin="dense"
              id="defrostabsTime"
              label="제상 절대 시간"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("defrostabsTime")}
            /> */}
            {watch("defrostabs")&&(
              <>
                {formData.map((data, index) => (
                  <div key={data.id}>
                    <TextField
                      label={`제상 절대 시간 ${index + 1}`}
                      type="time"
                      {...register(`defrostabsTimes[${index}].absTime`)}
                      defaultValue={getValues(`defrostabsTimes[${index}].absTime`)}
                      onChange={(e) => {
                        setValue(`defrostabsTimes[${index}].absTime`, e.target.value);
                        handleChange(data.id, 'absTime', e.target.value);
                      }}
                    />
                    <Button onClick={() => handleRemoveInput(data.id)}>삭제</Button>
                  </div>
                ))}
                <Button variant="contained" onClick={handleAddInput}>+ 시간대 추가</Button>
                <TextField
                  margin="dense"
                  id="defrostabsDuration"
                  label="제상절대유지시간"
                  fullWidth
                  variant="standard"
                  {...register("defrostabsDuration")}
                />

              </>
            )}
            
            <TextField
              margin="dense"
              id="out"
              label="실외기실 외부온도"
              fullWidth
              variant="standard"
              {...register("out")}
            />
            <TextField
              margin="dense"
              id="size"
              label="냉장고 표면적"
              fullWidth
              variant="standard"
              {...register("size")}
            />
            <TextField
              margin="dense"
              id="mass"
              label="냉장고 내부 총 질량"
              fullWidth
              variant="standard"
              {...register("mass")}
            />
            <TextField
              select
              label="계절"
              {...register("season")}
              value={watch("season", "1")} // 기본값 "1" 설정
              fullWidth
              variant="standard"
            >
              {seasonOption.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <FormControlLabel
            control={
            <Checkbox{...register("showAnnotations")}
            />
          }label="그래프에 주석 표시"/>
            <TextField
              type="time"
              margin="dense"
              id="startTime"
              label="그래프 시작 시간"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("startTime")}
            />
            <TextField
              type="time"
              margin="dense"
              id="endTime"
              label="그래프 끝 시간"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("endTime")}
            />
            {/* <TextField
              type="time"
              margin="dense"
              id="doorTime"
              label="문 열림 이벤트"
              fullWidth
              variant="standard"
              InputLabelProps={{ shrink: true }}
              {...register("doorTime")}
            /> */}
            문 열림 이벤트 시간 추가<br></br>
            <TextField
              type="time"
              value={doorTime}
              onChange={(e) => setDoorTime(e.target.value)}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <Button onClick={handleAddDoorOpenEvent}>문 열림 이벤트 추가</Button>
            <ul>
              {doorOpenTimes.map((time, index) => (
                <li key={index}>
                  {time}
                  <Button onClick={() => handleRemoveDoorOpenEvent(index)}>제거</Button>
                </li>
              ))}
            </ul>
            <TextField
              margin="dense"
              id="currentIn"
              label="냉장고 현재 내부온도 (℃)"
              fullWidth
              variant="standard"
              {...register("currentIn")}
            />
            <TextField
              margin="dense"
              id="currentOut"
              label="냉장고 현재 외부온도 (℃)"
              fullWidth
              variant="standard"
              {...register("currentOut")}
            />
            시간대 별 설정온도
            <input
              type="file"
              accept=".csv, application/json"
              onChange={handleFileUpload}
            />

            {formData.map((data, index) => (
              <div key={data.id}>
                <TextField
                  label={`시작 시간 ${index + 1}`}
                  type="time"
                  {...register(`schedules[${index}].startTime`)}
                  defaultValue={getValues(`schedules[${index}].startTime`)}
                  onChange={(e) => {
                    setValue(`schedules[${index}].startTime`, e.target.value);
                    handleChange(data.id, 'startTime', e.target.value);
                  }}
                />
                <TextField
                  label={`끝 시간 ${index + 1}`}
                  type="time"
                  {...register(`schedules[${index}].endTime`)}
                  defaultValue={getValues(`schedules[${index}].endTime`)}
                  onChange={(e) => {
                    setValue(`schedules[${index}].endTime`, e.target.value);
                    handleChange(data.id, 'endTime', e.target.value);
                  }}
                />
                <TextField
                  label={`설정 온도 ${index + 1}`}
                  type="standard"
                  {...register(`schedules[${index}].temperature`)}
                  defaultValue={getValues(`schedules[${index}].temperature`)}
                  onChange={(e) => {
                    // react-hook-form의 상태 업데이트
                    setValue(`schedules[${index}].temperature`, e.target.value);
                    // 사용자 정의 상태 업데이트
                    handleChange(index, 'temperature', e.target.value); // 여기서 data.id 대신 index 사용
                  }}
                />


                <Button onClick={() => handleRemoveInput(data.id)}>삭제</Button>
              </div>
            ))}
            <Button variant="contained" onClick={handleAddInput}>+ 시간대 추가</Button>

            {/* 시간대별 외부온도 입력 필드들 */}
            <br></br>
            시간대 별 외부온도 (편의점 내부온도)
            {outdoorTemps.map((temp, index) => (
              <Box key={temp.id} sx={{ display: 'flex', gap: 2, marginBottom: 2 }}>
                <TextField
                  label={`시작 시간 ${index + 1}`}
                  type="time"
                  value={temp.start}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'start', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <TextField
                  label={`끝 시간 ${index + 1}`}
                  type="time"
                  value={temp.end}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'end', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <TextField
                  label={`외부 온도 ${index + 1}`}
                  type="standard"
                  value={temp.temp}
                  onChange={(e) => handleChangeOutdoorTemp(temp.id, 'temp', e.target.value)}
                  InputLabelProps={{ shrink: true }}
                />
                <Button variant="outlined" color="error" onClick={() => handleRemoveOutdoorTemp(temp.id)}>
                  삭제
                </Button>
              </Box>
            ))}
            <Button variant="contained" onClick={handleAddOutdoorTemp}>+ 외부온도 추가</Button>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>취소</Button>
            {/* <Link to={"/result"}> */}
            <Button type="submit">그리기</Button>
            {/* </Link> */}
          </DialogActions>
        </form>
      </Dialog>
    </>
  );
}