import { Typography } from "@mui/material";
import ApexChart from "react-apexcharts";


export default function Graph({ title, xData, yData, yData2 = [], yAxisTitle = "", startHour = 0, endHour = 24, showAnnotations = true  }) {
  // 시간대에 따른 주석의 색상을 결정하는 함수
  const getAnnotationColor = (hour) => {
    // 각 시간대별 색상을 정의
    if (hour >= 0 && hour < 8 || hour >= 22 && hour <= 24) return '#008FFB'; // 파란색
    if (hour >= 8 && hour < 9 || hour >= 12 && hour < 16 || hour >= 19 && hour < 22) return '#FF0000'; // 빨간색
    if (hour >= 9 && hour < 12 || hour >= 16 && hour < 19) return '#FFFF00'; // 노란색
    return null;
  };
  
  // 시간대에 따른 주석의 라벨을 결정하는 함수
  const getAnnotationLabel = (hour) => {
    // 각 시간대별 라벨을 정의
    if (hour >= 0 && hour < 8 || hour >= 22 && hour <= 24) return '경부하';
    if (hour >= 8 && hour < 9 || hour >= 12 && hour < 16 || hour >= 19 && hour < 22) return '중간부하';
    if (hour >= 9 && hour < 12 || hour >= 16 && hour < 19) return '최대부하';
    return null;
  };

  // 주석과 레이블을 위한 배열을 초기화
  const annotations = [];
  const pointAnnotations = []; // point 레이블용 배열 추가
  const yMin = Math.min(...yData) - 0.5; // yData의 최솟값에서 5를 뺀 값
  const yMax = Math.max(...yData) + 0.5; // yData의 최댓값에서 5를 더한 값


  // xData 배열을 순회하며 주석과 레이블을 생성
  xData.forEach((time, index) => {
    const hour = parseInt(time.split(":")[0]);
    const prevHour = parseInt(xData[index - 1]?.split(":")[0] || -1);
    if (hour >= startHour && hour < endHour) {
        const fillColor = getAnnotationColor(hour);
        const prevFillColor = getAnnotationColor(prevHour);
        const labelText = getAnnotationLabel(hour);
        if (fillColor && labelText) {
          if(showAnnotations){
            annotations.push({
              x: time,
              x2: xData[index + 1] || (hour + 1) + ":00",
              fillColor: fillColor,
              opacity: 0.2
          });
          }
            if (fillColor !== prevFillColor || index === 0) { // 현재 시간대와 이전 시간대의 색상이 다르거나 첫 번째 인덱스일 경우
                pointAnnotations.push({ // point 레이블 추가
                    x: time,
                    label: {
                        borderColor: 'transparent',
                        offsetY: -10, // 레이블의 위치를 조정
                        style: {
                            color: '#333',
                            background: 'transparent',
                        },
                        text: labelText
                    }
                });
            }
        }
    }
  }
  
  );

  // 컴포넌트가 렌더링하는 JSX를 반환
  return (
    <>
      <Typography ml={1} mt={1}>
        {title}
      </Typography>
      <ApexChart
        type="area"
        series={[
          {
            name: "내부온도",
            data: yData,
          },
          {
            name: "설정온도",
            data: yData2,
          }
        ]}
        options={{
          chart: {
            height: 200,
            type: "area",
            toolbar: {
              show: true,
              tools: {
                zoom: true,
                zoomin: true,
                zoomout: true,
                pan: true,
                reset: true
              }
            },
            zoom: {
              enabled: true,
              type: 'x', // 'x', 'y' 또는 'xy' 중 하나를 선택하여 줌의 방향을 결정
              autoScaleYaxis: true
            },

          },
          colors: ['#8B00FF', '#FF4560'],
          dataLabels: {
            enabled: false,
          },
          stroke: {
            curve: "smooth",
          },
          xaxis: {
            type: "string",
            categories: xData,
            labels: {
              show: true,
            },
            tickAmount:10,
            title: {
              text: "시간(hh:mm)",
              rotate: -90,
            }
          },
          yaxis: {
            min: yMin,
            max: yMax,
            labels: {
              formatter: (value) => {
                return value? value.toFixed(2):'0.00'; // 소수점 2자리로 반올림
              },
            },
            title: {
              text: yAxisTitle,
              rotate: -90,
            }
          },
          legend: {
            show: false,
          },
          fill: {
            type: "gradient",
            gradient: {
              shadeIntensity: 1,
              opacityFrom: 0.7,
              opacityTo: 0.9,
              stops: [0, 90, 100],
            },
          },
          annotations: {
            xaxis: annotations,
            points: pointAnnotations // point annotations 추가
          }
        }}
      />
    </>
  );
}